const {ccclass, property} = cc._decorator;

@ccclass
export default class DdzBaseComponent extends cc.Component {
    protected getNodeComponent<T extends cc.Component>(path:string, type:any):T{
        return Global.UIHelper.getComponent(this.node, path, type)
    }

    protected getChild(path:string):cc.Node{
        return Global.UIHelper.getChild(this.node, path)
    }
}
